<template>
    <div>
        <all-check v-if="currentStep === steps.findIndex(step=>step.key === 'allCheck')"/>
        <pay-info v-if="currentStep === steps.findIndex(step=>step.key === 'payInfo')"/>
        <phone-info v-if="currentStep === steps.findIndex(step=>step.key === 'phoneInfo')"/>

        <confirm v-if="currentStep === steps.findIndex(step=>step.key === 'confirm')"/>

        <pay-q-r v-if="currentStep === steps.findIndex(step=>step.key === 'payQR')"/>

        <pay-result v-if="currentStep === steps.findIndex(step=>step.key === 'payComplete')"/>
        <phone-result v-if="currentStep === steps.findIndex(step=>step.key === 'phoneComplete')"/>
        <detail v-if="currentStep === steps.findIndex(step=>step.key === 'detail')"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import payInfo from './components/pay-info';
import phoneInfo from './components/phone-info';
import allCheck from './components/all-check.vue';

import confirm from './components/confirm';
import payQR from './components/payQR';
import payResult from './components/pay-result';
import phoneResult from './components/phone-result';
import detail from './components/detail.vue';

export default {
    name: 'LifePayIndex',
    components: {
        allCheck,
        payInfo,
        phoneInfo,
        confirm,
        payQR,
        payResult,
        phoneResult,
        detail
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep'])
    },
    mounted () {
        if (this.BUSINESS_EN_NAME_CODE.paySDM === this.businessTypeCode) {
            this.setSteps([
                {
                    name: '输入缴费信息',
                    isActive: true,
                    visible: true,
                    key: 'payInfo'
                },
                {
                    name: '确认缴费信息',
                    isActive: false,
                    visible: true,
                    key: 'confirm'
                },
                {
                    name: '支付缴费',
                    isActive: false,
                    visible: false,
                    key: 'payQR'
                },
                {
                    name: '完成办理',
                    isActive: false,
                    visible: true,
                    key: 'payComplete'
                }
            ]);
        } else if (this.BUSINESS_EN_NAME_CODE.payTelephone === this.businessTypeCode) {
            this.setSteps([
                {
                    name: '输入手机号码',
                    isActive: true,
                    visible: true,
                    key: 'phoneInfo'
                },
                {
                    name: '支付缴费',
                    isActive: false,
                    visible: true,
                    key: 'payQR'
                },
                {
                    name: '完成办理',
                    isActive: false,
                    visible: true,
                    key: 'phoneComplete'
                }
            ]);
        } else if (this.BUSINESS_EN_NAME_CODE.queryPay === this.businessTypeCode) {
            this.setSteps([
                {
                    name: '输入缴费信息',
                    isActive: true,
                    visible: true,
                    key: 'allCheck'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: true,
                    key: 'detail'
                }
            ]);
        }
    }
};

</script>
<style lang="scss" scoped>

</style>
