<template>
    <div>
        <div class="kd-page-title">
            查询结果
        </div>
        <div class="kd-page-content-wrapper wrap">
            <el-table
                :data="payDetails"
                class="payDetailWrap"
                border
                size="medium"
                row-class-name="payDetailCell"
                header-cell-class-name="payDetailHead"
                style="width: 100%"
            >
                <el-table-column
                    prop="batchNo"
                    label="批次号"
                />
                <el-table-column
                    prop="tranDate"
                    label="缴费日期"
                />
                <el-table-column
                    prop="payAmt"
                    label="缴费金额"
                />
            </el-table>
            <div class="pagination">
                <div
                    :class="current <= 1 ? 'disablePage' : ''"
                    @click="prePage"
                >
                    上一页
                </div>
                <div
                    :class="current >= total ? 'disablePage' : ''"
                    @click="nextPage"
                >
                    下一页
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'LifePayDetail',
    data () {
        return {
            total: 1,
            current: 1,
            number: 5,
            data: []
        };
    },
    computed: {
        ...mapState('lifePay', {
            payDetails: 'payDetails'
        })
    },
    mounted () {
        this.updateData();
    },
    watch: {
        payDetails () {
            this.updateData();
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        preStep () {
            this.changeStep('up');
        },
        prePage () {
            if (this.current <= 1) {
                return false;
            }
            this.updateData(this.current + 1);
        },
        nextPage () {
            if (this.current >= this.total) {
                return false;
            }
            this.updateData(this.current - 1);
        },
        updateData (current) {
            if (current !== undefined) {
                this.current = current;
            }
            this.total = this.payDetails.length;
            const start = (this.current - 1) * this.number;
            const end = start + this.current - 1;
            this.data = this.payDetails.slice(start, end);
        }
    }
};
</script>

<style lang="scss" scoped>
    .payDetailWrap {
        background: none;
        height: 90%;
        color: #ffffff;

        ::v-deep .payDetailCell {
            color: #ffffff;

            ::v-deep .cell {
                font-size: 19.2px;
            }
        }

        ::v-deep .payDetailHead {
            color: #ffffff;
            height: 7vh;

            ::v-deep .cell {
                font-size: 19.2px;
            }
        }
    }

    .pagination {
        width: 100%;
        height: 10%;
        padding: 0 3vw;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .disablePage {
            color: #cccccc;
        }
    }
</style>
