<template>
    <div>
        <div class="kd-page-title">
            请输入手机号
        </div>
        <div class="kd-page-content-wrapper wrap">
            <div class="item">
                <div class="label">
                    运营商：
                </div>
                <div class="value">
                    <el-radio-group
                        v-model="typeModule"
                        class="moneyWrap typeWrap"
                    >
                        <el-radio-button
                            v-for="(item, index) in typeList"
                            :label="item.moduleId"
                            :key="index"
                        >
                            {{ item.moduleName }}
                        </el-radio-button>
                    </el-radio-group>
                </div>
            </div>

            <div class="item">
                <div class="label">
                    手机号码：
                </div>
                <div class="value">
                    <kd-full-input
                        :ime-type="3"
                        placeholder="请输入手机号"
                        :watch-data="getNumber"
                        class="numberWrap"
                        :limit-number="11"
                    />
                </div>
            </div>

            <div class="item">
                <div class="label money">
                    充值金额：
                </div>
                <div class="value">
                    <el-radio-group
                        v-model="money"
                        class="moneyWrap"
                    >
                        <el-radio-button
                            v-for="(item, index) in CONSTANT.phoneMoneyArr"
                            :label="item"
                            :key="index"
                        >
                            {{ item }}元
                        </el-radio-button>
                    </el-radio-group>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!(typeModule && number.length === 11 && money)"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { getBkModule, getPayUrl } from '@/service/convenient/lifePay';

export default {
    name: 'LifePayPhoneInfo',
    data () {
        return {
            isGet: false,
            typeModule: '',
            typeList: [],
            number: '',
            money: 50
        };
    },
    mounted () {
        this.getTypeList();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('lifePay', ['setModuleId', 'setPayResult', 'setNumber', 'setBkDetail']),
        async getTypeList () {
            const res = await getBkModule();
            if (res && res.code === '0') {
                res.result = res.result.filter((item) => {
                    return item.moduleId === '004' || item.moduleId === '008' || item.moduleId === '009';
                });
                this.typeList = res.result;
            }
        },
        getNumber (data) {
            this.number = data;
        },
        async nextStep () {
            const res = await getPayUrl({
                userNo: this.number,
                payAmt: this.money.toFixed(2),
                moduleId: this.typeModule
            });
            if (res && res.code === '0') {
                this.setNumber(this.number);
                this.setModuleId(this.typeModule);
                this.setPayResult(res.result);
                // this.setPayUrl(res.result.urlMsg);
                // this.setSeqNo(res.result.mapsSeqNo);
                this.setBkDetail({
                    totalMoney: this.money
                });
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .selectWrap {
            width: 100%;
            height: 100%;
            overflow: hidden;
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/

            ::v-deep .el-input {
                width: 100%;
                height: 100%;
            }

            ::v-deep .el-input__inner {
                width: 100%;
                height: 100%;
                background: none;
                border: none;
                font-size: 19.2px;
            }

            ::v-deep .el-select .el-input .el-select__caret {
                width: auto;
                font-size: 32px;
            }
        }

        .item {
            display: flex;
            width: 70%;
            min-height: 5vw;
            margin-bottom: 3vh;

            .label {
                font-size: 22.4px;
                width: 20%;
                display: flex;
                align-items: center;
                justify-content: flex-end;
            }

            .label.money {
                height: 5vw;
            }

            .value {
                width: 80%;
            }
        }

        .numberWrap {
            color: #606266;
            font-size: 2.4vh;
            width: 100%;
            height: 100%;
        }

        .moneyWrap {
            width: 100%;
            display: flex;
            flex-wrap: wrap;

            ::v-deep .el-radio-button {
                display: flex;
                margin-right: 22px;
                margin-bottom: 22px;
                width: 10vw;
                height: 5vw;

                > span {
                    border-radius: 5px;
                    background: linear-gradient(180deg, rgba(112, 163, 216, 1) 0%, rgba(58, 132, 191, 1) 100%);
                    border: 1PX solid #ffffff;/*no*/
                    color: #ffffff;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 0;
                    font-size: 19.2px;
                }
            }

            ::v-deep .el-radio-button.is-active {
                > span {
                    background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
                }
            }
        }

        .typeWrap {
            ::v-deep .el-radio-button {
                margin-right: 22px;
                margin-bottom: 0;
            }
        }
    }
</style>
