<template>
    <div>
        <div class="kd-page-title">
            支付缴费
        </div>
        <div class="kd-page-content-wrapper wrap">
            <p>缴费金额：￥{{ detail.totalMoney }}</p>
            <qriously
                class="payValue"
                :value="payResult.urlMsg"
                :size="260"
            />
            <p>打开微信或支付宝“扫一扫”功能，扫描上方二维码</p>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { getResult } from '@/service/convenient/lifePay';

export default {
    name: 'LifePayPayQR',
    computed: {
        ...mapState('lifePay', {
            detail: 'detail',
            number: 'number',
            moduleId: 'moduleId',
            payResult: 'payResult'
        })
    },
    data () {
        return {
            interval: null
        };
    },
    mounted () {
        this.startCheck();
    },
    beforeDestroy () {
        clearInterval(this.interval);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        startCheck () {
            this.interval = setInterval(async () => {
                const res = await getResult({
                    type: 0,
                    userNo: this.number,
                    moduleId: this.moduleId,
                    oriMapsSeqNo: this.payResult.mapsSeqNo
                });
                if (res && res.code === '0') {
                    if (res.result.payFeeDetailResList[0].tranStatus === '0') {
                        this.changeStep('down');
                    }
                }
            }, 9000);
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        p {
            font-size: 25.6px;
        }

        .payValue {
            margin: 20px 0;
            background: #ffffff;
            width: 260px;
            height: 260px;
        }
    }

</style>
