<template>
    <div>
        <div class="kd-page-title">
            确认缴费信息
        </div>
        <div class="kd-page-content-wrapper wrap">
            <div class="year">
                <p>{{ detail.resultTime }}</p>
                <p>￥{{ detail.totalMoney }}</p>
            </div>
            <div class="list">
                <div class="item">
                    <p class="label">
                        缴费单位：
                    </p>
                    <p class="value">
                        {{ detail.companyName }}
                    </p>
                </div>
                <div class="item">
                    <p class="label">
                        账户户号：
                    </p>
                    <p class="value">
                        {{ number }}
                    </p>
                </div>
                <div class="item">
                    <p class="label">
                        户名：
                    </p>
                    <p class="value">
                        {{ detail.userName }}
                    </p>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="立即缴款"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { getPayUrl } from '@/service/convenient/lifePay';

export default {
    name: 'LifePayConfirm',
    computed: {
        ...mapState('lifePay', {
            detail: 'detail',
            moduleId: 'moduleId',
            number: 'number'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('lifePay', ['setPayResult']),
        preStep () {
            this.changeStep('up');
        },
        async nextStep () {
            const res = await getPayUrl({
                bkArrearsInfoId: this.detail.bkArrearsInfoId,
                userNo: this.number,
                payAmt: this.detail.totalMoney.toFixed(2),
                moduleId: this.moduleId,
                corrtradeDate: this.detail.corrtradedate,
                corrserialNo: this.detail.corrserialNo
            });
            if (res && res.code === '0') {
                this.setPayResult(res.result);
                // this.setPayUrl(res.result.urlMsg);
                // this.setSeqNo(res.result.mapsSeqNo);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .year {
            width: 50vw;
            height: 8vw;
            background: rgba(255, 255, 255, 0.1);
            border: 1PX dotted #ffffff;/*no*/
            margin-bottom: 5vh;
            display: flex;
            justify-content: space-between;
            align-items: center;

            p {
                padding: 0 20px;
                font-size: 19.2px;
            }
        }

        .list {
            width: 50vw;

            .item {
                display: flex;
                margin-bottom: 3vh;
                align-items: center;

                .label {
                    width: 35%;
                    font-size: 22.4px;
                    text-align: right;
                }

                .value {
                    font-size: 25.6px;
                    width: 80%;
                }
            }
        }
    }
</style>
