<template>
    <div>
        <div class="kd-page-title">
            充值成功
        </div>
        <div class="kd-page-content-wrapper wrap">
            <div class="list">
                <div class="item">
                    <p class="label">
                        业务名称：
                    </p>
                    <p class="value">
                        话费充值
                    </p>
                </div>
                <div class="item">
                    <p class="label">
                        业务流水号：
                    </p>
                    <p class="value">
                        {{ payResult.mapsSeqNo }}
                    </p>
                </div>
                <div class="item">
                    <p class="label">
                        充值日期：
                    </p>
                    <p class="value">
                        {{ payResult.tranDate }}
                    </p>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
        </div>
