</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'LifePayPhoneResult',
    computed: {
        ...mapState('lifePay', {
            detail: 'detail',
            moduleId: 'moduleId',
            payResult: 'payResult'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        preStep () {
            this.changeStep('up');
        },
        nextStep () {
            this.changeStep('down');
        }
    }
};
</script>

