        .list {
            width: 50vw;

            .item {
                display: flex;
                margin-bottom: 3vh;
                align-items: center;

                .label {
                    width: 35%;
                    font-size: 22.4px;
                    text-align: right;
                }

                .value {
                    font-size: 25.6px;
                    width: 80%;
                }
            }
        }

    }

</style>
