import request from '@/utils/request';

export const getBkModule = (params) => {
    return request.ajax('/ssm/bankMgt/getBkModule', 'get', params);
};

export const getInfo = (params) => {
    return request.ajax('/ssm/bankMgt/arrearsInfo', 'post', params);
};

export const getPayUrl = (params) => {
    return request.ajax('/ssm/bankMgt/aggregatePay', 'post', params);
};

export const getResult = (params) => {
    return request.ajax('/ssm/bankMgt/arrearsDetail', 'post', params);
};
