<template>
    <div>
        <div class="kd-page-title">
            请输入缴费信息
        </div>
        <div class="kd-page-content-wrapper wrap">
            <kd-full-input
                :placeholder="CONSTANT.locationName"
                class="numberWrap"
                :disabled="true"
            />
            <el-select
                v-model="typeModule"
                placeholder="请选择"
                class="selectWrap"
                popper-class="payTypeWrap"
                :loading="isGet"
            >
                <el-option
                    v-for="item in typeList"
                    :key="item.moduleId"
                    :label="item.moduleName"
                    :value="item.moduleId"
                />
            </el-select>
            <kd-full-input
                :ime-type="3"
                placeholder="请输入缴费户号"
                :watch-data="getNumber"
                class="numberWrap"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!(typeModule && number)"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { getBkModule, getInfo } from '@/service/convenient/lifePay';

export default {
    name: 'LifePayPayInfo',
    data () {
        return {
            isGet: false,
            typeModule: '',
            typeList: [],
            number: null
        };
    },
    mounted () {
        this.getTypeList();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('lifePay', ['setBkDetail', 'setModuleId', 'setNumber']),
        async getTypeList () {
            this.isGet = true;
            const res = await getBkModule();
            this.isGet = false;
            if (res && res.code === '0') {
                res.result = res.result.filter((item) => {
                    return !(item.moduleId === '004' || item.moduleId === '008' || item.moduleId === '009');
                });
                this.typeList = res.result;
                this.typeModule = this.typeList[0].moduleId;
            }
        },
        getNumber (data) {
            this.number = data;
        },
        preStep () {
            this.changeStep('up');
        },
        async nextStep () {
            const res = await getInfo({
                moduleId: this.typeModule,
                userNo: this.number
            });
            if (res && res.code === '0') {
                if (JSON.stringify(res.result) === '{}') {
                    this.changeTipPage({
                        message: '此户号查无欠费，请继续保持！',
                        enableBack: true,
                        type: 1
                    });
                    return;
                }
                this.setBkDetail(res.result);
                this.setNumber(this.number);
                this.setModuleId(this.typeModule);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .selectWrap {
            margin-top: 20px;
            width: 50vw;
            height: 6vw;
            overflow: hidden;
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/

            ::v-deep .el-input {
                width: 100%;
                height: 100%;
            }

            ::v-deep .el-input__inner {
                width: 100%;
                height: 100%;
                background: none;
                border: none;
                font-size: 19.2px;
            }

            ::v-deep .el-select .el-input .el-select__caret {
                width: auto;
                font-size: 32px;
            }
        }

        .numberWrap {
            margin-top: 20px;
            color: #606266;
            font-size: 2.4vh;
            width: 50vw;
            height: 6vw;
        }
    }
</style>
